.form {
	&__field {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 28px;
		@include m {
			display: block;
			text-align: center;
			margin-bottom: 20px;
			max-width: 600px;
			margin-left: auto;
			margin-right: auto; }
		input, textarea {
			background-color: $white;
			border: 1px solid $blue;
			color: $black;
			@include rob;
			font-weight: 100;
			font-size: 18px;
			padding: 5px 10px;
			@include placeholder {
				color: $black; } }
		input {
			width: 263px;
			height: 42px;
			@include lg {
				width: 200px; } }
		textarea {
			min-height: 183px;
			resize: none; }
		&--full {
			display: block;
			input, textarea {
				display: block;
				width: 100%;
				@include lg {
					width: 100%; } } } }

	&__juctify-line {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		@include m {
			display: block;
			text-align: center; } } }
