.header {
	padding-top: 14px;
	position: absolute;
	left: 0;
	top: 0;
	z-index: 3;
	width: 100%;
	min-height: 88px;
	overflow: hidden;
	@include m {
		position: static; }
	.l {
		&:before, &:after {
			content: '';
			position: absolute;
			bottom: 3px;
			height: 100%;
			background:  $red;
			width: 2000px;
			height: 35px;
			transform-origin: 50% 50%;
			transform: skew(25deg);
			z-index: 1;
			@include m {
				display: none; } }
		&:before {
			right: 1154px; }
		&:after {
			left: 415px; } }
	&__inner {
		@include clr; }
	&__logo {
		float: left;
		font-size: 0;
		padding-top: 5px;
		@include m {
			float: none;
			text-align: center;
			margin-bottom: 20px; }
		@include xs {
			padding-right: 30px; }
		a {
			display: inline-block;
			vertical-align: top;
			padding-top: 2px; }
		p {
			margin-left: 14px;
			display: inline-block;
			vertical-align: top;
			text-align: center;
			line-height: 1.1;
			@include robc;
			font-weight: 700;
			font-size: 28px;
			color: $red2;
			@include xs {
				font-size: 14px;
				margin-top: 7px; }
			span {
				font-size: 37px;
				@include xs {
					font-size: 14px; } } }
		img {
			@include xs {
				width: 70px; } } }
	&__right {
		float: right;
		width: 652px;
		@include lg {
			width: 565px; }
		@include l {
			width: 500px; }
		@include m {
			float: none;
			width: 100%; } }
	&__top {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;
		@include m {
			display: block;
			text-align: center;
			margin-bottom: 20px; } }
	&__langs {
		font-size: 0;
		@include m {
			position: absolute;
			right: 10px;
			top: 0px; }
		a {
			display: inline-block;
			vertical-align: top;
			width: 32px;
			height: 32px;
			border: 1px solid transparent;
			font-size: 18px;
			@include robc;
			color: $black;
			text-align: center;
			padding-top: 5px;
			margin-right: 11px;
			@include xs {
				display: block;
				right: 0;
				width: 20px;
				height: 20px;
				font-size: 14px;
				padding-top: 2px;
				margin-bottom: 5px; }
			&:last-of-type {
				margin-right: 0; }
			&.is-active {
				color: $red2;
				border-color: $red;
				background-color: $white; } } }
	&__link {
		font-weight: 300;
		margin-top: 3px;
		@include robc;
		color: $black;
		@include m {
			margin-right: 10px; }
		@include xs {
			margin-right: 0;
			display: block;
			margin-bottom: 10px; }
		i {
			position: relative;
			top: 3px;
			margin-right: 5px; }
		svg {
			fill: $red;
			font-size: 19px; } }
	&__nav {
		font-size: 0;
		padding-left: 46px;
		position: relative;
		z-index: 2;
		text-align: left;
		@include lg {
			padding-left: 20px; }
		@include m {
			background: $red;
			text-align: center;
			padding-left: 0; }
		li {
			display: inline-block;
			vertical-align: top;
			// margin-right: 26px
			// &:last-of-type
			// 	margin-right: 0
			&.is-active {
				a {
					background: $red-d;
					@include m {
						background: none; } } } }
		a {
			font-weight: 300;
			color: $white;
			font-size: 18px;
			padding: 6px 35px 0px;
			height: 35px;
			// background: $red
			display: block;
			transform-origin: 50% 50%;
			transform: skew(25deg);
			@include trans;
			@include lg {
				padding: 6px 20px 0px; }

			@include m {
				padding: 10px;
				height: auto;
				transform: skew(0deg); }
			span {
				display: block;
				transform-origin: 50% 50%;
				transform: skew(-25deg);
				@include m {
					transform: skew(0deg); } }



			&:hover {
				background: $red-d; } } } }


